.post {
    width: 100%;
    max-width: 786px;
    margin: 0 auto;
    padding: 60px 10px;
    font-size: 20px;
    line-height: 140%;
    color: #020202;
}

.post__main-image {
    padding: 0 0 47px;
}

.post__date {
    display: block;
    padding: 0 0 15px;
    color: #2F37BA;
}

.post h2 {
    margin: 0;
    padding: 0 0 22px;
    font-weight: bold;
    font-size: 50px;
    line-height: 100%;
}

.post p {
    margin: 0;
    padding: 0 0 27px;
}

.post ol {
    margin: 0;
    padding: 0 0 27px 40px;
    list-style: decimal;
}

.post ul {
    margin: 0;
    padding: 0 0 27px 40px;
    list-style: disc;
}

.post li {
    margin: 0;
    padding: 0;
    color: #2F37BA;
}

.post li span {
    margin: 0;
    padding: 0;
    color: #020202;
}

.post__image-wrapper {
    display: flex;
    justify-content: space-between;
}

.post__image-wrapper figure {
    width: 373px;
    padding: 0;
}

.post figcaption {
    font-size: 16px;
    color: #8B8B8B;
    padding: 5px 0 30px;
}

.post blockquote {
    position: relative;
    margin: 0 0 34px;
    padding: 0;
    border-top: 1px solid @base-color;
    border-bottom: 1px solid @base-color;
}

