.post blockquote p {
    padding: 13px 0;
    font-size: 24px;
    line-height: 140%;
}

.post h3 {
    margin: 0;
    padding: 28px 0;
    font-weight: bold;
    font-size: 40px;
    line-height: 100%;
}

.post__share {
    display: flex;
    align-items: center;
    padding: 5px 0 20px;
}

.post__share-text {
    font-weight: bold;
    font-size: 16px;
    color: #000000;
}

.post__share-list {
    display: flex;
    list-style: none !important;
    margin: 0 !important;
    padding: 0 !important;
}

.post__share-item-link {
    display: block;
    margin: 0 0 0 16px;
    line-height: 0;
}

.post__share-item-link svg {
    fill: #2F37BA;
}

.post__share-item-link svg:hover {
    fill: @base-color;
}

@media (max-width: @tablet-width) {
    .post {
        padding: 20px 10px;
        font-size: 14px;
    }
    
    .post__main-image {
        padding: 0 0 26px;
    }
    
    .post__date {
        padding: 0 0 13px;
    }
    
    .post h2 {
        font-size: 24px;
    }
    
    .post ol {
        padding: 0 0 27px 32px;
    }
    
    .post ul {
        padding: 0 0 27px 32px;
    }
    
    .post__image-wrapper {
        flex-direction: column;
        align-items: center;
    }
    
