.blog {
    width: 100%;
}

.blog__wrapper {
    position: relative;
    max-width: 1180px;
    margin: 0 auto;
    padding: 60px 10px 0;
}

.blog__sorting {
    position: absolute;
    top: -110px;
    right: 10px;
}

.select-selected {
    position: relative;
    width: 188px;
    padding: 15px 30px;
    color: #fff;
    background: transparent;
    border: 1px solid #fff;
    cursor: pointer;

    &:hover {
        color: @base-color;
    }
}

.select-selected:after {
    position: absolute;
    content: "";
    top: 50%;
    right: 30px;
    width: 10px;
    height: 10px;
    transform: translateY(-50%);
    background: url("../img/blog/arrow.svg") center no-repeat;
}

.select-selected.select-arrow-active:after {
    transform: translateY(-50%) rotate(180deg);
}

.select-items {
    display: none;
    transition: all .3s;
}

.select-items div {
    width: 188px;
    padding: 15px 30px;
    color: #000;
    background: #fff;
    border: 1px solid #fff;
    cursor: pointer;

    &:hover {
        font-weight: 600;
    }
}

.select-show {
    display: block;
}

.blog__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.blog__item {
    width: 32%;
    max-width: 373px;
    min-height: 500px;
    margin: 0 0 40px;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.06);

    &:hover {
        box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.15);
    }
}

.blog__item--hidden {
    display: none;
}

.blog__item-image {
    height: 201px;
    object-fit: cover;
}

.blog__item:nth-child(3n) {
    margin: 0 0 40px;
}

.blog__item-wrapper {
    min-height: 300px;
    display: flex;
    flex-direction: column;
    padding: 24px 20px 20px;
}

.blog__item-date {
    margin: 0;
    padding: 0 0 10px;
    font-size: 16px;
    line-height: 140%;
    color: #4f4f4f;
}

.blog__item-title {
    margin: 0;
    padding: 0 0 10px;
    font-weight: bold;
    font-size: 22px;
    line-height: 120%;
    color: #000000;

    &:hover {
        color: @base-color;
    }
}

.blog__item-description {
    margin: 0;
    font-weight: bold;
    font-size: 16px;
    line-height: 140%;
    color: #000000;
    flex-grow: 1;
}

.blog__item-link {
    display: inline-block;
    width: 134px;
    padding: 8px 0;
    background: @base-color;
    border: none;

    &:hover {
        background: #e16c48;
        color: #fff;
        border: none;
    }
}

.blog__load-more-button {
    display: block;
    width: 195px;
    margin: 20px auto 80px;
    padding: 18px 0;

    &:hover {
        background: #fff;
        border-color: #e16c48;
        color: #e16c48;
    }
}

@media (max-width: @desktop-width) {
    .blog__sorting {
        top: -78px;
    }
}

@media (max-width: @tablet-width-large) {
    .blog__sorting {
        top: -73px;
    }

    .blog__list {
        justify-content: space-around;
    }

    .blog__item {
        width: 49%;
    }
}

@media (max-width: @tablet-width) {
    .blog__sorting {
        top: -66px;
    }

    .blog__item {
        width: 100%;
    }
}